export default {
	data() {
		return {
			text: ""
		}
	},
	onShow: function() {
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "insertBBSSuccess") {
				uni.showToast({
					icon: "success",
					title: "发布成功"
				})
			}
		},
		textareaAInput: function(e) {
			this.text = e.detail.value
		},
		send: function () {
			if (this.text != "") {
				getApp().sendSocket("insertBBS", {
					"bbs_text": this.text
				})
			}
		}
	}
}
